import { axios } from '@/utils/http';
import {getTeacher} from '../../api/teacher';
const state = {
  marketTeacher: [],
  orgList:[]
};

const mutations = {
  saveTeacher (state, data) {
    state.marketTeacher = [...data.data.teachers];
  },
  saveOrglist (state, data) {
    state.orgList = [...data.data];
  }
};

const actions = {
  async getTeacher (context, params = {}) {
    context.commit('saveTeacher', await getTeacher({params}));
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
