import { axios } from '@/utils/http';
import HOST from '@/config/host';

export function getTeacher(options){
  return axios.get(HOST + '/market_teacher/getTeacher', options);
}

export function addTeacherApi(options){
  return axios.post(HOST + '/market_teacher/addTeacher', options);
}
export function delTeacher(options){
  return axios.post(HOST + '/market_teacher/delTeacher', options);
}