<template>
  <!-- <transition> -->
    <!-- <div v-if="isSelectOrgShow" class="selectOrg">
      <div @click="close" class="close">X</div> -->
      <el-cascader :props="props" v-model="val" :show-all-levels="false"></el-cascader>
      <!-- <el-tree 
				ref="tree" 
				node-key="orgCode" 
				@node-click="nodeClick" 
				:props="props" 
				:load="loadNode" 
				:expand-on-click-node="false"
				lazy> -->
			<!-- </el-tree> -->
    <!-- </div> -->
  <!-- </transition> -->
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  props: {
    vlaue: {
      default: ''
    }
  },
  data() {
    window.org = this;
    return {
      val: this.vlaue || '',
			props: {
        label: 'orgName',
        value: "orgCode",
        isLeaf: 'isLeaf',
        checkStrictly: true,
        lazy: true,
        lazyLoad: async (node, resolve) => {
          let orgCode = node.data ? node.data.orgCode : undefined;
          let data = await this.getOrgData(orgCode);
          // 检测是否有子节点
          for(let item of data){
            let result = await this.getOrgData(item.orgCode);
            item.leaf = result.length > 0 ? false : true;
          }
          return resolve(data);
        }
			}
		};
  },
  watch: {
    vlaue (newValue) {
      console.log(newValue)
      this.val = newValue
    },
    val (newVal) {
      this.$emit('input', newVal[newVal.length - 1])
    }
  },
  created () {
    console.log(this.vlaue)
  },
  computed: {
    ...mapState("selectOrg", ["isSelectOrgShow",'selectedNode'])
  },
  methods: {
		...mapMutations("selectOrg", ["setState"]),
		...mapActions('selectOrg', ['getOrgData']),
		async loadNode(node, resolve){
			let orgCode = node.data ? node.data.orgCode : undefined;
			let data = await this.getOrgData(orgCode);
			// 检测是否有子节点
			for(let item of data){
				let result = await this.getOrgData(item.orgCode);
				item.isLeaf = result.length > 0 ? false : true;
			}
			return resolve(data);
		},
		nodeClick(node){
			this.setState({selectedNode: node});
		},
    close() {
      this.setState({ isSelectOrgShow: false });
    }
  }
};
</script>

<style scoped>
.close {
  text-align: right;
  padding: 20px;
  cursor: pointer;
  color: #333;
}
.selectOrg {
  position: absolute;
  right: 0;
  width: 300px;
  top: 0;
  bottom: 0;
  background: #fff;
  z-index: 100;
}
.v-enter {
  transform: translateX(100%);
}
.v-enter-active {
  transition: transform 0.5s ease-in-out 0s;
}
.v-leave-active {
  transition: transform 0.2s ease-in-out 0s;
  transform: translateX(100%);
}
</style>
